<template>
    <div class="wrapper" ref="wrapper">
      <div>
          <slot></slot>
      </div>
    </div>
</template>

<script>
    import BS from 'better-scroll'
    export default {
        name: "scroll",
        data(){
            return{
                scrollBs:{}
            }
        },
        mounted() {
            this.scrollBs = new BS(this.$refs.wrapper,{
               probeType:3,
               click:true
             })
        }
    }
</script>

<style scoped>
    .wrapper{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
</style>