<template>
    <div class="home" ref="home">
        <nav-tab @setvalue="getSong"/>
        <div class="content" >
                <song-list v-loading="!isLoading"
                           element-loading-text="在加载,别搞颜色"
                           element-loading-background="rgba(0, 0, 0, 0.05)"
                           element-loading-spinner="el-icon-loading"
                           @songid="getId"
                           :song="multimaSong"/>
            <lrc-list ref="lrc" class="lrc" :list="currentLyrsc"/>
            <div class="bg" v-bind:style="{backgroundImage:bgImage}">
            </div>
        </div>
        <div class="control">
            <control ref="control" @criTIme="speTime"/>
        </div>

        <audio :src="songUrl" @canplay="duration"
               @timeupdate="current($event)" ref="audio"></audio>

    </div>
</template>

<script>
    import NavTab from "./NavTab";
    import songList from "./songList";
    import control from "./control";
    import lrcList from "./lrcList";
    import Lyric from 'lyric-parser'

    import {search, songId, lyrc, banner} from '../network/netWork'

    export default {
        name: "Home",
        data(){
            return {
                input: null,
                multimaSong: [],
                pages: 1,
                isLoading: true,
                songUrl: '',
                play: false,
                currentLyrsc: {},
                nextId: null,
                banner: '',
                home:null
            }
        },
        mounted(){
            this.input = this.$refs.input;
            this.home = this.$refs.home;
        },
        computed:{
            bgImage(){
                return `url("${this.banner}")`
            }

        },
        methods:{
            //歌曲列表加载
            getSong(value){
                const _vue = this;
                this.isLoading = false;
                if(value.length <= 0 ) return;
                    _vue.getdetail(value);
            },
            //获取对应keywords关键字歌曲
            getdetail(value){
                search(value).then( res => {
                    this.multimaSong = res.data.result.songs;
                    this.isLoading = true;
                }).catch( err =>{
                    return err
                })
            },

            //d点击获取id，请求url和歌词
            getId(id ,index){

                //判断最后和最前的歌曲
                if(index > 0){
                    this.nextId = this.multimaSong[index-1].id;
                }
                if(index <= this.multimaSong.length-1){
                    this.nextId = this.multimaSong[index+1].id;
                }
                //获取歌曲banner
                banner(id).then( res =>{
                    this.banner = res.data.songs[0].al.picUrl;
                }).catch( err => err);


                //获取歌曲ID
                songId(id).then(res =>{
                    this.songUrl = res.data.data[0].url;
                    setTimeout(()=>{
                        this.$refs.control.toggleSong();
                    },100)
                }).catch(err => err);

                //处理歌词
                lyrc(id).then( res =>{
                   this.currentLyrsc =  new Lyric(res.data.lrc.lyric,this.$refs.lrc.handler);
                    if(!this.currentLyrsc) return;
                    this.currentLyrsc.stop();
                    this.currentLyrsc.pause();
                }).catch( err => err)
            },
            //获取总时长
            duration(){
                this.$refs.control.audioTime();
            },
            //当前播放时间
            current(e){
               this.$refs.control.centTime(e.target.currentTime);
            },
            //进度条快进歌词跟着滚动
            speTime(time){
                if(this.currentLyrsc){
                    this.$parent.currentLyrsc.seek(time * 1000);
                }
            }
        },
        components:{
            NavTab,
            songList,
            control,
            lrcList
        }
    }
</script>

<style scoped>
  .home{
      width: 800px;
      height: 500px;
      box-shadow: 0px 0px 10px rgba(0,0,0,.3);
      margin:0 auto;
      position: absolute;
      transform: translate(-50%,-50%);
      left: 50%;
      top: 50%;
      z-index:9999999;
      overflow: hidden;
  }
    .content{
        width: 100%;
        display: flex;
        position: relative;
        height: calc(90% - 40px);
        background: rgba(0,0,0,.1);
    }
    .control{
        position: relative;
        z-index: 999999;
        width: 100%;
        height: 40px;
        background: white;
        border-top: 1px solid rgba(0,0,0,.095);
    }
    .bg{
        width: 100%;
        height:100%;
        position: absolute;
        z-index: -1;
        background-color: rgba(0, 0, 0,.3);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 0%;
        -webkit-filter: blur(65px);
        filter: blur(25px);
        opacity: .9;
    }
    .lrc{
        width: 70%;
        height: 100%;
        overflow: hidden;
    }
</style>