import netwrok from './index'

function  searchData(key) {
    return netwrok({
        url:'/search/suggest',
        params:{
            keywords:key,
            type:'mobile'
        }
    })

}
function  search(key) {
    return netwrok({
        url:'/search',
        params:{
            keywords:key
        }
    })
}

function  songId(id) {
    return netwrok({
        url:'/song/url',
        params:{
            id
        }
    })
}

function lyrc(id){
   return netwrok({
        url:'/lyric',
       params:{
            id
       }
    })
}


function banner(ids){
    return netwrok({
        url:'/song/detail',
        params:{
            ids
        }
    })
}
export {
    searchData,
    search,
    songId,
    lyrc,
    banner
}