<template>
    <div class="max">
        <div class="left div">
            <div @click="upSong">
                <img src="../assets/up.png" alt="">
            </div>
            <div @click="toggleSong">
                <img  v-show="!togglePlay" src="../assets/sus.png" alt="1">
                <img v-show="togglePlay" src="../assets/play.png" alt="2">
            </div>
            <div @click="nextSong">
                <img style="transform: rotateZ(180deg)" src="../assets/up.png" alt="">
            </div>
        </div>
        <div class="center">
            <div>
                <span ref="current">00:00</span>
            </div>
            <div class="sppend">
                <div ref="bar" @click.stop="jump($event)">
                    <p ref="p" :style="{width:width}"></p>
                    <i :style="{left:width}"
                          ref="span"></i>
                </div>
            </div>
            <div>
                <span ref="duration">00:00</span>
            </div>
        </div>
        <div class="right div"></div>
    </div>
</template>

<script>
    export default {
        name: "control",
        props:{
        },
        data(){
            return {
                isWidth:0,
                demo:null,
                time:null,
                togglePlay:true
            }
        },
        computed:{
            width(){
                return this.isWidth + 'px'
            }
        },
        watch:{

        },
        methods:{
            //点击快进
            jump(e){
                this.isWidth = e.offsetX;
                let  speend = (e.offsetX * this.time) / e.target.offsetWidth ;
                this.$parent.$refs.audio.currentTime = speend;
                this.$emit('criTIme',speend);
            },
            //进度条移动
            scodeX(time){
               let percen = (time / this.time) * this.$refs.bar.offsetWidth;
               this.isWidth = percen;
            },
            audioTime(){
                //获取总时长
                this.time = this.$parent.$refs.audio.duration;
                let minute = Math.floor(this.time / 60);
                minute = minute.toString().length <= 1 ? `0${minute}` : `${minute}`;
                let second = Math.round(this.time % 60).toFixed(0);

               // 直接赋值给dom元素，赋值给data有些不是响应式
               this.$refs.duration.innerText =  second >=10 ? `${minute}:${second}` : `${minute}:0${second}`;

               this.$nextTick(()=>{
                   this.$refs.duration.innerText =  second >=10 ? `${minute}:${second}` : `${minute}:0${second}`;
               })
            },
            //监听当前播放时间
            centTime(time){
                time = Math.floor(time);
                let second = time%60;
                let minutes = Math.floor(time/60);
                second = second < 10 ? `0${second}` : second;
                minutes = minutes < 10 ?  `0${minutes}`: minutes ;
                // 直接赋值给dom元素，赋值给data有些不是响应式
                this.$refs.current.innerText = `${minutes}:${second}`;
                //进度条滚动，传递当前时间
                this.scodeX(time);
            },
            //上一曲
            upSong(){

            },
            //播放暂停
            toggleSong(){
                if(this.$parent.$refs.audio.paused){
                    this.$parent.$refs.audio.play();
                }else{
                    this.$parent.$refs.audio.pause();
                }
                this.togglePlay = this.$parent.$refs.audio.paused;
                this.$parent.currentLyrsc.togglePlay();
            },
            //下一曲
            nextSong(){

            }
        }
    }
</script>

<style scoped>
  .max{
      display: flex;
      width: 100%;
      height: 100%;
  }
  .max .left{
      display: flex;
      width: 240px;
  }
  .max .left div{
      flex: 1;
      text-align: center;
      border-radius: 50%;
  }
  .left div img{
      display: inline-block;
      margin-top: 5px;
      width: 40%;
  }
  .max .right{
      width: 80px;
  }
  .max .center{
      font-size: .6rem;
      font-weight: 550;
      color: #5e5a5a;
      display: flex;
      flex:1 ;
  }
  .center div{
      text-align: center;
      line-height: 40px;
      height: 100%;
      width: 60px;
  }
  .center  .sppend{
       display: flex;
       align-items: center;
        flex: 1;
    }
  .center span{
      -moz-user-select:none; /*火狐*/
      -webkit-user-select:none; /*webkit浏览器*/
      -ms-user-select:none; /*IE10*/
      -khtml-user-select:none; /*早期浏览器*/
      user-select:none;
  }
    .sppend div{
        margin:5px;
        position: relative;
        height: 3px;
        border-radius: 5px;
        width: 100%;
        background: silver;
    }
  .sppend div i{
      width: 10px;
      height: 10px;
      display: block;
      cursor: pointer;
      border-radius: 50%;
      background: #C20C0C;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(-50%,-50%);
  }
  .sppend div p{
      margin: 0;
      border-radius: 5px;
      width: 0;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background: #970707;
  }
</style>