<template>
    <div class="navtab">
        <h1 class="logo">
        </h1>
        <div class="search">
                <el-input
                        v-model="value"
                        clearable
                        size="small"
                        prefix-icon="el-icon-search"
                        placeholder="请输入歌曲"
                        @input="querySearch"
                />

            <ul class="list" v-show="value.length > 0">
                <span class="span"></span>
                <li v-for="item in searchSong"
                    :key="item.keyword"
                @click="handleSelect(item.keyword)">
                    <span style="color: #C20C0C">{{value}}</span>
                    <span>{{item.keyword.replace(value,'')}}</span>
                </li>
                <p v-show='!searchSong'>没有该歌曲</p>
            </ul>
        </div>

    </div>
</template>

<script>
    import {searchData} from '../network/netWork'

    export default {
        name: "NavTab",
        data(){
            return{
                value:'',
                isfocus:false,
                searchSong:[]
            }
        },
        methods:{
            querySearch(value){
                if (value <= 0) return;
                searchData(value).then( data => {
                    this.searchSong = data.data.result.allMatch;
                });
            },
            handleSelect(item){
                this.value = '';
                this.$emit('setvalue',item)
            }
        }
    }
</script>

<style scoped>
    li,ul{
        list-style: none;
        margin: 0;
        padding: 0;
    }
 .navtab{
     background:#C20C0C;
     width:100%;
     height: 10%;
     display: flex;
     align-items: center;
 }
    .logo{
        margin: 0 0 0 30px;
        height: 100%;
        width: 20%;
        background: url("../assets/topbar.png")no-repeat 5px 3%;
        background-size:140%;
    }
    .search{
        position: relative;
        margin-left: 180px;
    }
    .list{
        position: absolute;
        top: 52px;
        left: 0;
        width: 94%;
        box-shadow: 0px 0px 5px rgba(0,0,0,.1);
        border: solid 1px rgba(0,0,0,.1);
        min-height: 50px;
        height: auto;
        background: #fff;
        z-index: 2;
        font-size: .5rem;
        color: silver;
    }
 .list .span{
     position: absolute;
     background: #fff;
     display: block;
     width: 15px;
     left: 10%;
     top: -7.5px;
     z-index: -1;
     height: 15px;
     transform: rotateZ(45deg);
     border: solid 1px rgba(0,0,0,.1);
     border-right:none ;
     border-bottom:none ;
 }
.list li{
  cursor: pointer;
    padding: 10px 5px;
  margin: 5px 0;
}
    .list li:hover{
        background: rgba(0,0,0,.1);
        color: #5e5a5a;
    }
</style>