<template>
    <scroll ref="scroll">
        <ul class="list" ref="ul">
            <li class="lyric" ref="p" :class="{'active':currentLineNum === index-1}"
                v-for="(item,index) in list.lines" :key="(index*(Math.random()+Math.random()))">
                {{item.txt}}
            </li>
        </ul>
    </scroll>
</template>

<script>
    import scroll from "./scroll";


    export default {
        name: "lrcList",
        props:{
            list:{
                type:Object,
                default(){
                    return {}
                }
            }
        },
        components:{
            scroll
        },
        methods:{
            handler({lineNum}) {
                this.currentLineNum = lineNum;
                if (lineNum > 3) {
                    let num =  lineNum -3 ;
                    this.$refs.scroll.scrollBs.scrollToElement(this.$refs.p[num],1000 ,0 )
                } else {
                    this.$refs.scroll.scrollBs.scrollToElement(0 ,0, 1000 )
                }
            }
        },
        data(){
            return{
                currentLineNum:null,
                height:60
            }
         }
    }

</script>

<style scoped>
    *{
        margin: 0;
        padding: 0;
    }
  .list{
      list-style: none;
      text-align: center;
      font-size: 1.5vw;
      padding: 0 10%;
  }
    .list .lyric{
        color: white;
        font-weight: 600;
        user-select:none;
        line-height: 60px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .lyric.active{
        color: sandybrown;
    }
</style>