import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { Button, Input, Icon, Autocomplete, Col, Loading, Slider  } from 'element-ui';

Vue.use(Autocomplete);
Vue.use(Loading );
Vue.use(Button);
Vue.use(Slider)
Vue.use(Input);
Vue.use(Icon);
Vue.use(Col);

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
