<template>
    <div class="songList">
        <ul>
            <li v-for="(item, index) in song" :key="item.id" @click="songId(item.id,index)">
                <div style=" flex: 1;">
                    <span>{{item.name}}</span>
                </div>
                <div class="title">
                    <span>{{item.artists[0].name}}</span>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "songList",
        props:{
            song:{
                type:Array,
                default(){
                    return []
                }
            }
        },
        methods:{
            songId(id, index){
                this.$emit('songid',id,index)
            }
        }
    }
</script>

<style scoped>
    ul,li{
        margin:0;
        list-style: none;
    }
  .songList{
      width: 30%;
      height: 100%;
      overflow: hidden;
      padding-bottom:40px ;
  }
    .songList ul{
        width: 94%;
        height:98%;
        overflow-y:auto ;
        padding: 3%;
        font-size:.5rem ;
        color: #fff;
        font-weight: 500;
    }

    ::-webkit-scrollbar {
        width: 5px;
    }
    /*<!--修改 滚动条的 下面 的 样式-->*/
        ::-webkit-scrollbar-track {
            background-color: deepskyblue;
            -webkit-border-radius: 2em;
            -moz-border-radius: 2em;
            border-radius: 2em;
        }
    /*<!--修改 滑块 -->*/
    ::-webkit-scrollbar-thumb {
        background-color: lightcoral;
        -webkit-border-radius: 2em;
        -moz-border-radius: 2em;
        border-radius: 2em;
    }
    .songList ul li{
        display: flex;
        cursor: pointer;
        padding: 8px 0;
    }
    .songList ul li:hover{
        color: #000;
        background: rgba(0,0,0,.1);
    }
    .songList ul li div{
        padding-left: 5px;
        text-align: left;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
    .songList ul li .title{
        width: 60px;
    }

</style>